import React, { Component } from 'react';
import classes from './PosterBuilder.css';
import Poster from '../../components/Poster/Poster';
import PosterControl from '../../components/Poster/PosterControl/PosterControl';
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';



class PosterBuilder extends Component {
    state = {
        title: '',
        subTitle: '-',
        enTitle: '2017 China Tennis Club League',
        colorChoosed: '#03A0EA',
        qcodeSrc: 'www.baidu.com'
    };
    titleEditHandler = (event) => {
        this.setState({
            title: event.target.value
        })
    };
    subTitleEditHandler = (event) => {
        this.setState({
            subTitle: event.target.value
        })
    };
    enTitleEditHandler = (event) => {
        this.setState({
            enTitle: event.target.value
        })
    };
    colorEditHandler = (event) => {
        this.setState({
            colorChoosed: event.target.value
        })
    };
    qcodeEditHandler = (event) => {
        this.setState({
            qcodeSrc: event.target.value
        })
    }

    printPDF = () => {
        console.log('click')
        const input = document.getElementById('divToPrint');
        // let width = 595;
        // let height = 842;
        // const type = "png";
        // const scaleBy = 3;
        // const canvas = document.createElement('canvas');
        // canvas.width = width * scaleBy;
        // canvas.height = height * scaleBy;
        // canvas.style.width = width * scaleBy + 'px';
        // canvas.style.height = height * scaleBy + 'px';
        // const context = canvas.getContext('2d');
        // context.scale(scaleBy, scaleBy);
        html2canvas(input)
            .then((canvas) => {
                const imgData = canvas.toDataURL('image/png');
                const pdf = new jsPDF('', 'pt', 'a4');
                pdf.addImage(imgData, 'JPEG', 0, 0, 595.28, 595.28 / canvas.width * canvas.height);
                // pdf.output('dataurlnewwindow');
                pdf.save("qcodePoster.pdf");
            });
    }

    render() {
        return (
            <div className={classes.PosterBuilder}>
                <div id="divToPrint">
                    <Poster
                        titleText={this.state.title}
                        subTitleText={this.state.subTitle}
                        enTitleText={this.state.enTitle}
                        colorStyle={this.state.colorChoosed}
                        qcodeSrcText={this.state.qcodeSrc} />
                </div>
                <PosterControl
                    titleEdit={this.titleEditHandler}
                    subTitleEdit={this.subTitleEditHandler}
                    enTitleEdit={this.enTitleEditHandler}
                    colorEdit={this.colorEditHandler}
                    qcodeEdit={this.qcodeEditHandler}
                    clicked={this.printPDF}
                />
                

            </div>
        );
    }
}

export default PosterBuilder;


